body {
  letter-spacing: -.5px;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}

button {
  float: left;
  cursor: pointer;
  background: none;
  border: 0;
  outline: none;
  width: 40px;
  height: 40px;
  padding: 0;
}

#audio-player-container {
  --seek-before-width: 0%;
  --volume-before-width: 100%;
  --buffered-width: 0%;
  background: #fff;
  width: 95%;
  max-width: 500px;
  height: 132px;
  margin: 100px 2.5% auto;
  position: relative;
}

#audio-player-container:before {
  z-index: -1;
  content: "";
  background: linear-gradient(to left, #007db5, #ff8a00);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  position: absolute;
  top: -2px;
  left: -2px;
}

p {
  background: #fff;
  margin: 0;
  padding: 0 5px;
  font-size: 28px;
  position: absolute;
  top: -18px;
  right: 5%;
}

#play-icon {
  margin: 20px 2.5% 10px;
}

path {
  stroke: #007db5;
}

.time {
  float: left;
  text-align: center;
  width: 37px;
  margin: 28.5px 0 18.5px;
  font-size: 20px;
  display: inline-block;
}

output {
  float: left;
  clear: left;
  text-align: center;
  width: 32px;
  margin: 10px 2.5% 0 5%;
  font-size: 20px;
  display: inline-block;
}

#volume-slider {
  width: 58%;
  margin: 10px 2.5%;
}

#volume-slider::-webkit-slider-runnable-track {
  background: #007db599;
}

#volume-slider::-moz-range-track {
  background: #007db599;
}

#volume-slider::-ms-fill-upper {
  background: #007db599;
}

#volume-slider:before {
  width: var(--volume-before-width);
}

#mute-icon {
  margin: 0 2.5%;
}

input[type="range"] {
  float: left;
  -webkit-appearance: none;
  outline: none;
  width: 48%;
  height: 19px;
  margin: 30px 2.5% 20px;
  padding: 0;
  position: relative;
}

input[type="range"]::-webkit-slider-runnable-track {
  cursor: pointer;
  background: linear-gradient(to right, #007db599 var(--buffered-width), #007db533 var(--buffered-width));
  width: 100%;
  height: 3px;
}

input[type="range"]:before {
  cursor: pointer;
  width: var(--seek-before-width);
  content: "";
  background-color: #007db5;
  height: 3px;
  position: absolute;
  top: 8px;
  left: 0;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  box-sizing: content-box;
  background-color: #fff;
  border: 1px solid #007db5;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin: -7px 0 0;
  position: relative;
}

input[type="range"]:active::-webkit-slider-thumb {
  background: #007db5;
  transform: scale(1.2);
}

input[type="range"]::-moz-range-track {
  cursor: pointer;
  background: linear-gradient(to right, #007db599 var(--buffered-width), #007db533 var(--buffered-width));
  width: 100%;
  height: 3px;
}

input[type="range"]::-moz-range-progress {
  background-color: #007db5;
}

input[type="range"]::-moz-focus-outer {
  border: 0;
}

input[type="range"]::-moz-range-thumb {
  cursor: pointer;
  box-sizing: content-box;
  background-color: #fff;
  border: 1px solid #007db5;
  border-radius: 50%;
  width: 15px;
  height: 15px;
}

input[type="range"]:active::-moz-range-thumb {
  background: #007db5;
  transform: scale(1.2);
}

input[type="range"]::-ms-track {
  cursor: pointer;
  color: #0000;
  background: none;
  border: solid #0000;
  width: 100%;
  height: 3px;
}

input[type="range"]::-ms-fill-lower {
  background-color: #007db5;
}

input[type="range"]::-ms-fill-upper {
  background: linear-gradient(to right, #007db599 var(--buffered-width), #007db533 var(--buffered-width));
}

input[type="range"]::-ms-thumb {
  cursor: pointer;
  box-sizing: content-box;
